<template>
  <div>
    <div class="uploadContainer" v-show="showFlag === 1">
      <div :style="`display: ${templateList.length>0 ? 'flex' : 'block'};`">
        <div v-if="templateList.length>0" style="min-width: 200px; max-width: 400px;text-align: left;margin-right: 10px;">
          <div>
            <span style="color: #E27471;margin-right: 4px;">*</span>
          </div>
          <el-radio-group v-model="templateValue" @change="templateChange">
            <el-radio
              v-for="item in templateListPermission"
              :key="item.value"
              :label="item.value"
              :title="item.label"
              style="display: flex; margin-top: 10px;"
            >{{item.label}}</el-radio>
          </el-radio-group>
        </div>
        <el-upload
          class="upload-demo"
          ref="upload"
          action="api/tiji-system/post/import"
          :headers="headers"
          drag
          :on-change="uploadByJsqd"
          :on-remove="handleRemove"
          accept=".xlsx"
          :file-list="fileList"
          :auto-upload="false"
        >
          <i class="el-icon-upload"></i>
          <div class="el-upload__text"></div>
        </el-upload>
      </div>
      <div class="uplloadTips">
        <div class="uploadTipsTitle"></div>
        <p class="uploadTipsContent">
          <el-button
            type="text"
            @click="download"
          >{{templateList.length>0 ? (templateList.find(v=>v.value===templateValue)||{}).templateName :templateName}}</el-button>
        </p>
        <div class="uploadTipsDetail">
          <p class="uploadTipsDetailTitle"></p>
          <p>1</p>
          <p>2</p>
          <p>310M</p>
          <p v-if="isRickControl">4</p>
        </div>
      </div>
    </div>
    <div class="uploadEreor" v-show="showFlag === 2">
      <div>
        <div class="uploadEreorImg">
          <img v-if="recordContent.successNum > 0" :src="successImg" />
          <img v-if="recordContent.successNum <= 0" :src="errorImg" />
        </div>
        <div class="uploadEreorTips" v-show="recordContent.total>0">
          {{ recordContent.total }}
          <span style="color: #5072FF">{{recordContent.successNum }}</span>
          <span style="color: red">{{ recordContent.errorNum }}</span>
        </div>
        <div class="uploadEreorTips" v-show="recordContent.total==0"></div>
        <div
          v-show="recordContent.errorNum && recordContent.errorNum > 0"
          @click="downLoadEorre"
          class="uploadEreorBtn"
        ></div>
      </div>
    </div>
    <div class="uploadEreor" v-show="showFlag === 3">
      <div>
        <div class="uploadEreorImg">
          <img :src="errorImg" />
        </div>
        <p>{{ msg }}</p>
      </div>
    </div>
  </div>
</template>
<script>
import { errorImg, successImg } from './util'
export default {
  props: {
    token: {
      type: String,
      default: ''
    },
    showFlag: {
      type: Number,
      default: 1
    },
    recordContent: {
      type: Object,
      default() {
        return {}
      }
    },
    templateList: {
      type: Array,
      default() {
        return []
      }
    },
    templateName: {
      type: String,
      default: ''
    },
    fileList: {
      type: Array,
      default: []
    },
    msg: {
      type: String,
      default: ''
    },
    // 
    isRickControl: {
        type: Boolean,
        default: false
    }
  },
  data() {
    return {
      headers: {
        'Blade-Auth': 'bearer ' + this.token
      },
      formdata: null,
      successImg: successImg,
      errorImg: errorImg,
      templateValue: 1
    }
  },
  computed: {
      templateListPermission() {
          if(this.templateList.length===0) return []
          return this.templateList.filter(v=> v.permissionImport)
      }
  },
  methods: {
    templateChange(val) {
      this.$emit('getTemplateValue', val)
    },
    //
    download() {
      this.$emit('downloadTemplate')
    },
    //
    downLoadEorre() {
      this.$emit('downLoadEorreResult')
    },
    //
    beforeAvatarUpload(file) {
      let acceptType = ['xlsx']
      let type = file.name
        .split('.')
        .slice(-1)[0]
        .toLowerCase()
      if (!acceptType.includes(type)) {
        this.$emit('setFileList', [])
        this.$message.warning('xlsx')
        return
      }
      if (file.size > 10 * 1024 * 1024) {
        this.$emit('setFileList', [])
        this.$message.warning('10MB!')
        return false
      }
      return true
    },
    //
    uploadByJsqd(file, fileList) {
      //beforeAvatarUpload
      if (this.beforeAvatarUpload(file)) {
        if (fileList.length > 0) {
          // this.fileList = [fileList[fileList.length - 1]]// 
          this.$emit('setFileList', [fileList[fileList.length - 1]])
        }
        this.formdata = new FormData()
        this.formdata.append('file', file.raw)
        this.$emit('transmitList', this.formdata)
      }
    },
    //
    handleRemove(file, fileList) {
      this.formdata = new FormData()
      this.$emit('transmitList', this.formdata)
    }
  }
}
</script>
